import Message from './message';
import { messageField } from './helpers';
import { getStaticProperty, isIE10 } from '../utils';

// jsdoc-ignore-start
@messageField(['_lctext', '_lcattrs'])
// jsdoc-ignore-end
export default class TypedMessage extends Message {
  /**
   * æ‰€æœ‰å†…ç½®çš„å¯Œåª’ä½“æ¶ˆæ¯å‡ç»§æ‰¿è‡ªæœ¬ç±»
   * @extends Message
   */
  constructor() {
    super();
    this._ = {};
  }

  /**
   * @type {Number}
   * @readonly
   */
  get type() {
    return this.constructor.TYPE;
  }

  /** @type {String} */
  set text(text) {
    return this.setText(text);
  }
  get text() {
    return this.getText();
  }

  /** @type {Object} */
  set attributes(attributes) {
    return this.setAttributes(attributes);
  }
  get attributes() {
    return this.getAttributes();
  }

  /**
   * @param {String} text
   * @return {TypedMessage} self
   */
  setText(text) {
    this._lctext = text;
    return this;
  }
  /**
   * @return {String}
   */
  getText() {
    return this._lctext;
  }

  /**
   * @param {Object} attributes
   * @return {TypedMessage} self
   */
  setAttributes(attributes) {
    this._lcattrs = attributes;
    return this;
  }
  /**
   * @return {Object}
   */
  getAttributes() {
    return this._lcattrs;
  }

  _getCustomFields() {
    const fields = Array.isArray(this.constructor._customFields)
      ? this.constructor._customFields : [];
    return fields.reduce((result, field) => {
      if (typeof field !== 'string') return result;
      result[field] = this[field]; // eslint-disable-line no-param-reassign
      return result;
    }, {});
  }

  _getType() {
    throw new Error('not implemented');
  }

  toJSON() {
    return Object.assign({
      _lctext: this.getText(),
      _lcattrs: this.getAttributes(),
    }, this._getCustomFields(), this._getType());
  }

  /**
   * è§£æžå¤„ç†æ¶ˆæ¯å†…å®¹
   * <pre>
   * ä¸ºç»™å®šçš„ message è®¾ç½® text ä¸Ž attributes å±žæ€§ï¼Œè¿”å›žè¯¥ message
   * å¦‚æžœå­ç±»æ²¡æœ‰æä¾› messageï¼Œnew this()
   * @protected
   * @param  {Object}  json    json æ ¼å¼çš„æ¶ˆæ¯å†…å®¹
   * @param  {TypedMessage} message å­ç±»æä¾›çš„ message
   * @return {TypedMessage}
   * @implements AVMessage.parse
   */
  static parse(json, message = new this()) {
    message.content = json; // eslint-disable-line no-param-reassign
    const customFields = isIE10
      ? getStaticProperty(message.constructor, '_customFields')
      : message.constructor._customFields;
    let fields = Array.isArray(customFields) ? customFields : [];
    fields = fields.reduce((result, field) => {
      if (typeof field !== 'string') return result;
      result[field] = json[field]; // eslint-disable-line no-param-reassign
      return result;
    }, {});
    Object.assign(message, fields);
    return super.parse(json, message);
  }
}
