import { default as d } from 'debug';

const debug = d('LC:ConversationQuery');

export default class ConversationQuery {
  static _encode(value) {
    if (value instanceof Date) {
      return { __type: 'Date', iso: value.toJSON() };
    }
    if (value instanceof RegExp) {
      return value.source;
    }
    return value;
  }

  static _quote(s) {
    return `\\Q${s.replace('\\E', '\\E\\\\E\\Q')}\\E`;
  }

  static _calculateFlag(options) {
    return [
      'withLastMessagesRefreshed',
      'compact',
    ].reduce(
      (prev, key) => (prev << 1) + (Boolean)(options[key]),
      0
    );
  }

  /**
   * Create a ConversationQuery
   * @param  {IMClient} client
   */
  constructor(client) {
    this._client = client;
    this._where = {};
    this._extraOptions = {};
  }

  _addCondition(key, condition, value) {
    // Check if we already have a condition
    if (!this._where[key]) {
      this._where[key] = {};
    }
    this._where[key][condition] = this.constructor._encode(value);
    return this;
  }

  toJSON() {
    const json = {
      where: this._where,
      flag: this.constructor._calculateFlag(this._extraOptions),
    };
    if (typeof this._skip !== 'undefined') json.skip = this._skip;
    if (typeof this._limit !== 'undefined') json.limit = this._limit;
    if (typeof this._order !== 'undefined') json.sort = this._order;
    debug(json);
    return json;
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼ŒæŒ‡å®šèŠå¤©å®¤çš„ç»„å‘˜åŒ…å«æŸäº›æˆå‘˜å³å¯è¿”å›ž
   * @param {string[]} peerIds - æˆå‘˜ ID åˆ—è¡¨
   * @return {ConversationQuery} self
   */
  containsMembers(peerIds) {
    return this.containsAll('m', peerIds);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼ŒæŒ‡å®šèŠå¤©å®¤çš„ç»„å‘˜æ¡ä»¶æ»¡è¶³æ¡ä»¶çš„æ‰è¿”å›ž
   *
   * @param {string[]} - æˆå‘˜ ID åˆ—è¡¨
   * @param {Boolean} includeSelf - æ˜¯å¦åŒ…å«è‡ªå·±
   * @return {ConversationQuery} self
   */
  withMembers(peerIds, includeSelf) {
    const peerIdsSet = new Set(peerIds);
    if (includeSelf) {
      peerIdsSet.add(this._client.id);
    }
    this.sizeEqualTo('m', peerIdsSet.size);
    return this.containsMembers(Array.from(peerIdsSet));
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µæ»¡è¶³ç­‰äºŽæ¡ä»¶æ—¶å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param value
   * @return {ConversationQuery} self
   */
  equalTo(key, value) {
    this._where[key] = this.constructor._encode(value);
    return this;
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µæ»¡è¶³å°äºŽæ¡ä»¶æ—¶å³å¯è¿”å›ž
   * @param {string} key
   * @param value
   * @return {ConversationQuery} self
   */
  lessThan(key, value) {
    return this._addCondition(key, '$lt', value);
  }


  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µæ»¡è¶³å°äºŽç­‰äºŽæ¡ä»¶æ—¶å³å¯è¿”å›ž

   * @param {string} key
   * @param value
   * @return {ConversationQuery} self
   */
  lessThanOrEqualTo(key, value) {
    return this._addCondition(key, '$lte', value);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µæ»¡è¶³å¤§äºŽæ¡ä»¶æ—¶å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param value
   * @return {ConversationQuery} self
   */

  greaterThan(key, value) {
    return this._addCondition(key, '$gt', value);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µæ»¡è¶³å¤§äºŽç­‰äºŽæ¡ä»¶æ—¶å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param value
   * @return {ConversationQuery} self
   */

  greaterThanOrEqualTo(key, value) {
    return this._addCondition(key, '$gte', value);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µæ»¡è¶³ä¸ç­‰äºŽæ¡ä»¶æ—¶å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param value
   * @return {ConversationQuery} self
   */
  notEqualTo(key, value) {
    return this._addCondition(key, '$ne', value);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µå¯¹åº”çš„å€¼åŒ…å«åœ¨æŒ‡å®šå€¼ä¸­æ—¶å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param values
   * @return {ConversationQuery} self
   */
  containedIn(key, values) {
    return this._addCondition(key, '$in', values);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µå¯¹åº”çš„å€¼ä¸åŒ…å«åœ¨æŒ‡å®šå€¼ä¸­æ—¶å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param values
   * @return {ConversationQuery} self
   */
  notContainsIn(key, values) {
    return this._addCondition(key, '$nin', values);
  }
  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“conversationçš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µä¸­çš„å…ƒç´ åŒ…å«æ‰€æœ‰çš„å€¼æ‰å¯è¿”å›ž
   *
   * @param {string} key
   * @param values
   * @return {ConversationQuery} self
   */
  containsAll(key, values) {
    return this._addCondition(key, '$all', values);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µå¯¹åº”çš„å€¼åŒ…å«æ­¤å­—ç¬¦ä¸²å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param {string} subString
   * @return {ConversationQuery} self
   */
  contains(key, subString) {
    return this._addCondition(key, '$regex', ConversationQuery._quote(subString));
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µå¯¹åº”çš„å€¼ä»¥æ­¤å­—ç¬¦ä¸²èµ·å§‹å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param {string} prefix
   * @return {ConversationQuery} self
   */
  startsWith(key, prefix) {
    return this._addCondition(key, '$regex', `^${ConversationQuery._quote(prefix)}`);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µå¯¹åº”çš„å€¼ä»¥æ­¤å­—ç¬¦ä¸²ç»“æŸå³å¯è¿”å›ž
   *
   * @param {string} key
   * @param {string} suffix
   * @return {ConversationQuery} self
   */
  endsWith(key, suffix) {
    return this._addCondition(key, '$regex', `${ConversationQuery._quote(suffix)}$`);
  }

  /**
   * å¢žåŠ æŸ¥è¯¢æ¡ä»¶ï¼Œå½“ conversation çš„å±žæ€§ä¸­å¯¹åº”çš„å­—æ®µå¯¹åº”çš„å€¼æ»¡è¶³æä¾›çš„æ­£åˆ™è¡¨è¾¾å¼å³å¯è¿”å›ž
   *
   * @param {string} key
   * @param {RegExp} regex
   * @return {ConversationQuery} self
   */
  matches(key, regex) {
    this._addCondition(key, '$regex', regex);
    // Javascript regex options support mig as inline options but store them
    // as properties of the object. We support mi & should migrate them to
    // modifiers
    let _modifiers = '';
    if (regex.ignoreCase) { _modifiers += 'i'; }
    if (regex.multiline) { _modifiers += 'm'; }

    if (_modifiers && _modifiers.length) {
      this._addCondition(key, '$options', _modifiers);
    }
    return this;
  }

  /**
   * æ·»åŠ æŸ¥è¯¢çº¦æŸæ¡ä»¶ï¼ŒæŸ¥æ‰¾ key ç±»åž‹æ˜¯æ•°ç»„ï¼Œè¯¥æ•°ç»„çš„é•¿åº¦åŒ¹é…æä¾›çš„æ•°å€¼
   *
   * @param {string} key
   * @param {Number} length
   * @return {ConversationQuery} self
   */
  sizeEqualTo(key, length) {
    return this._addCondition(key, '$size', length);
  }

  /**
   * è®¾ç½®è¿”å›žé›†åˆçš„å¤§å°ä¸Šé™
   *
   * @param {Number} limit - ä¸Šé™
   * @return {ConversationQuery} self
   */
  limit(limit) {
    this._limit = limit;
    return this;
  }

  /**
   * è®¾ç½®è¿”å›žé›†åˆçš„èµ·å§‹ä½ç½®ï¼Œä¸€èˆ¬ç”¨äºŽåˆ†é¡µ
   *
   * @param {Number} skip - èµ·å§‹ä½ç½®è·³è¿‡å‡ ä¸ªå¯¹è±¡
   * @return {ConversationQuery} self
   */
  skip(skip) {
    this._skip = skip;
    return this;
  }

  /**
   * è®¾ç½®è¿”å›žé›†åˆæŒ‰ç…§æŒ‡å®škeyè¿›è¡Œå¢žåºæŽ’åˆ—
   *
   * @param {string} key
   * @return {ConversationQuery} self
   */
  ascending(key) {
    this._order = key;
    return this;
  }

  /**
   * è®¾ç½®è¿”å›žé›†åˆæŒ‰ç…§æŒ‡å®škeyè¿›è¡Œå¢žåºæŽ’åˆ—ï¼Œå¦‚æžœå·²è®¾ç½®å…¶ä»–æŽ’åºï¼ŒåŽŸæŽ’åºçš„ä¼˜å…ˆçº§è¾ƒé«˜
   *
   * @param {string} key
   * @return {ConversationQuery} self
   */
  addAscending(key) {
    if (this._order) {
      this._order += `,${key}`;
    } else {
      this._order = key;
    }
    return this;
  }

  /**
   * è®¾ç½®è¿”å›žé›†åˆæŒ‰ç…§æŒ‡å®š key è¿›è¡Œé™åºæŽ’åˆ—
   *
   * @param {string} key
   * @return {ConversationQuery} self
   */
  descending(key) {
    this._order = `-${key}`;
    return this;
  }

  /**
   * è®¾ç½®è¿”å›žé›†åˆæŒ‰ç…§æŒ‡å®š key è¿›è¡Œé™åºæŽ’åˆ—ï¼Œå¦‚æžœå·²è®¾ç½®å…¶ä»–æŽ’åºï¼ŒåŽŸæŽ’åºçš„ä¼˜å…ˆçº§è¾ƒé«˜
   *
   * @param {string} key
   * @return {ConversationQuery} self
   */
  addDescending(key) {
    if (this._order) {
      this._order += `,-${key}`;
    } else {
      this._order = `-${key}`;
    }
    return this;
  }

  /**
   * è®¾ç½®è¿”å›žçš„ conversations åˆ·æ–°æœ€åŽä¸€æ¡æ¶ˆæ¯
   * @param  {Boolean} [enabled=true]
   * @return {ConversationQuery} self
   */
  withLastMessagesRefreshed(enabled = true) {
    this._extraOptions.withLastMessagesRefreshed = enabled;
    return this;
  }

  /**
   * @deprecated è¯·æ›¿æ¢ä¸º {@link ConversationQuery#withLastMessagesRefreshed}
   * @param  {Boolean} [enabled=true]
   * @return {ConversationQuery} self
   */
  withLastMessages(enabled) {
    console.warn('DEPRECATION ConversationQuery#withLastMessages: ' +
      'Use ConversationQuery#withLastMessagesRefreshed instead.');
    return this.withLastMessagesRefreshed(enabled);
  }

  /**
   * è®¾ç½®è¿”å›žçš„ conversations ä¸ºç²¾ç®€æ¨¡å¼ï¼Œå³ä¸å«æˆå‘˜åˆ—è¡¨
   * @param  {Boolean} [enabled=true]
   * @return {ConversationQuery} self
   */
  compact(enabled = true) {
    this._extraOptions.compact = enabled;
    return this;
  }

  /**
   * æ‰§è¡ŒæŸ¥è¯¢
   * @return {Promise.<Conversation[]>}
   */
  find() {
    return this._client._executeQuery(this);
  }
}
